import React, { FC } from "react";
import clsx from "clsx";

import { Button } from "components";
import { Ball, Coins } from "./components";
import img from "./img.svg";
import css from "./style.module.scss";

interface Props {
  play: boolean;
}

const CrashBanner: FC<Props> = ({ play }) => {
  return (
    <div className={clsx(css.container, !play && css.disable)}>
      <img src={img} className={css.img} />

      <p className={css.text}>
        place a bet and cashout at
        <span>{`â€¯1,000X`}</span>!
      </p>
      <span className={css.buttonWrapper}>
        <Button className={css.button}>play now</Button>
      </span>

      <Ball />
      <Coins />
    </div>
  );
};

export default CrashBanner;
