import React from "react";
// import css from "";

import ballHead from "./assets/ball.webp";
import meteor from "./assets/meteor.svg";
import lines from "./assets/lines.svg";
import cloud from "./assets/cloud.svg";
import cloud_1 from "./assets/cloud_1.svg";
import ballLine from "./assets/ballLine.svg";
import { useSassImport } from "hooks";

export const Ball = () => {
  const css = useSassImport(() => import("./style.module.scss"));

  if (!css.ballWrapper) {
    return null;
  }

  return (
    <>
      <img src={ballLine} className={css.ballLine} />
      <div className={css.ballWrapper}>
        <img src={lines} className={css.lines} />
        <img src={ballHead} className={css.ballHead} />
      </div>
      <img src={meteor} className={css.meteor} />
      <img src={cloud} className={css.cloud} />
      <img src={cloud_1} className={css.cloud_1} />
    </>
  );
};
