import clsx from "clsx";
import React from "react";

import _1 from "./imgs/_1.svg";
import _2 from "./imgs/_2.svg";
import _3 from "./imgs/_3.svg";
import _4 from "./imgs/_4.svg";
import _5 from "./imgs/_5.svg";
import css from "./style.module.scss";

const imgs = [_1, _2, _3, _4, _5];

export const SecondCoins = () => {
  return (
    <>
      {imgs.map((src, i) => {
        return (
          <img
            src={src}
            alt={`coin_${i}`}
            className={clsx(css.coin, css[`coin__${i + 1}`])}
          />
        );
      })}
    </>
  );
};
